export default {
    methods: {
        register: function() {
            alterBtns(['#reg', '#reset'])
            init()
            alterBtns(['#reg', '#reset'], false)
        }
    }
}

function getParams(pairs) {
    var data = new FormData();
    // eslint-disable-next-line
    pairs.forEach(function(value, index) {
        data.append(value.name, document.querySelector(value.id).value)
    })
    return data
}

function pair_radio(name_out, name_in) {
    var radios = document.getElementsByName(name_in)
    var val = ``;
    // eslint-disable-next-line
    radios.forEach(function(value, index) {
        if (value.checked) {
            val = `#${value.id}`;
        }
    })
    return { name: name_out, id: val }
}


function init() {
    let pairs = [
        { name: 'title', id: '#uTitle' },
        { name: 'fullname', id: '#uFullName' },
        { name: 'date', id: '#uDob' },
        { name: 'email', id: '#uEmail' },
        { name: 'state', id: '#uState' },
        { name: 'lga', id: '#uLgovt' },
        { name: 'marital_status', id: '#mStatus' },
        { name: 'address', id: '#uAddress' },
        { name: 'phone', id: '#uPhone' },
    ]
    var gender = pair_radio('gender', 'gender')
    var pc = pair_radio('physical_challenge', 'physical_challenge')

    pairs.push(gender)
    pairs.push(pc)

    var data = getParams(pairs)

    fetch('http://localhost:2500/register', {
        method: 'post',
        body: data
    }).then(function(response) {
        response.json().then(function(data) {
            if (data.error) {
                var param = data.param ? data.param : '';
                alert(`${param} ${data.message}`)
            } else {
                alert('success')
            }
        })
        // eslint-disable-next-line
    }).catch(function(error) {

    })
}

function alterBtns(ids, disable = true) {
    // eslint-disable-next-line
    ids.forEach(function(btn, index) {
        if (disable) {
            document.querySelector(btn).setAttribute('disabled', '')
        } else {
            document.querySelector(btn).removeAttribute('disabled')
        }
    })
}