import Vue from 'vue'
import App from './App.vue'
import router from './assets/js/router'

Vue.config.productionTip = false


/**
	handlerouter params
**/
router.beforeEach((to, from, next) => {
    document.title = `Vue - ${to.meta.title}`
    next()
    document.comp = to.name
})


new Vue({
    router,
    render: h => h(App)
}).$mount('#app')